html {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  -moz-text-size-adjust: none;
  text-size-adjust: none;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  line-height: 1.5;
}

h1, h2, h3, h4, button, input, label {
  line-height: 1.1;
}

h1, h2, h3, h4 {
  text-wrap: balance;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}

img, picture {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

textarea:not([rows]) {
  min-height: 10em;
}

:target {
  scroll-margin-block: 5ex;
}

.header {
  background: #fff;
  font-weight: 500;
  box-shadow: 0 2px 4px #0000000d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  align-items: center;
  display: flex;
}

.header__header {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: -4px;
  display: block;
  position: relative;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.container {
  grid-template-columns: repeat(auto-fill, minmax(166px, 200px));
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.product {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.product:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.product__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__title:hover {
  color: #34568b;
  transition: all .3s;
}

.product__code {
  color: #616070;
  margin-right: 64px;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product__rating {
  justify-content: space-between;
  align-items: flex-end;
  padding-top: 16px;
  display: flex;
}

.product__rating_reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.product__price_title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product__price_price {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px auto 0;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
